@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400&display=swap);
html,
body {
  font-family: "Roboto", sans-serif;
}

.appContent {
  min-height: 90vh;
}

.App {
  margin: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: space-between;
}

.appContentSmall {
  margin-top: 50px;
  min-height: 75vh;
}

.CardDesign {
    max-width: 500px;
    min-width: 300px;
    height: 300px;
    position: relative;
    overflow: hidden;
    border-radius: 20px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25);
    display: grid;
    grid-template-rows: 1fr 1fr;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.1) 0%, rgba(64, 64, 64, 0.1) 25%, rgba(255, 255, 255, 0.4) 100%);
    transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);
    align-self: center;
}

.CardDesign:hover {
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
    box-shadow: 0 30px 60px rgba(0, 0, 0, 0.5);
}

.CardDesign:hover img {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
}

.CardDesign img {
    position: absolute;
    top: 0;
    left: 0;
    width: 110%;
    height: 110%;
    background-position: center;
    z-index: -1;
    transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);
}

.CardDesign h3 {
    color: white;
    font-size: 30px;
    margin: 20px 0 0 20px;
    width: 80%;
}

.CardDesign p {
    color: rgba(255, 255, 255, 0.8);
    text-transform: uppercase;
    font-weight: 600;
    font-size: 18px;
    align-self: end;
    justify-self: start;
    background-color: #00adee;
    border-radius: 20px;
    padding-left: 10px;
    padding-right: 10px;
    margin: 0 0 20px 20px;
}

.date {
    color: #6297BA;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 12px;
    align-self: end;
    justify-self: start;
    background-color: #F1FBFF;
    border-radius: 10px;
    padding-left: 10px;
    padding-right: 10px;
    margin: 0 0 20px 20px;
}
.singleContent {
    display: flex;
    flex-direction: column;
    margin: 20px;
    align-items: center;

}

.singleTitle {
    font-size: 24px;
    font-weight: 700;
}

.singleImage {
    width: 80%;
    align-self: center;
}

.wordpressContent {
    display: flex;
    flex-direction: column;
    margin: 20px;
    max-width: 80%;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    align-items: center;
}

.wordpressContent video {
    align-self: center;
}

.wp-block-video {
    display: flex;
    flex-direction: column;
}
.footer {
  display: grid;
  grid-template-columns: 8fr 2fr;
  background: #00adee;
  padding: 1rem 5vw;
  color: white;
  margin-top: 50px;
}

.logo-wrapper-footer {
  display: grid;
  align-items: center;
  justify-items: left;
}

.menu-wrapper {
  display: grid;
  align-items: center;
  justify-items: left;
  font-size: xx-large;
  color: #f0fbff;
}

.social-wrapper-footer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  padding-left: 20px;
  align-items: center;
  justify-items: right;
  font-size: xx-large;
  grid-gap: 10px;
}

.social-wrapper-footer a {
  color: white;
}

